.post img{
    width: 50px;
    margin-right: 10px;
}
.post{
    padding: 15px;
    border: 1px solid white;
    margin: 10px auto;
    width: 80%;
}
.post .likes{
    background-color: rgb(58, 58, 158);
    width: 60px;
    padding: 10px;
    border-radius: 7px;
    display: inline-block;
}
.post .dislikes{
    margin-left: 5px;
    background-color: rgb(158, 58, 58);
    width: 75px;
    padding: 10px;
    border-radius: 7px;
    display: inline-block;
}
.delete{
    margin-left: 95%;
    color: red;
    background: none;
    border: none;
    outline: none;
    font-size: 25px;
    font-weight: bold;
}