.dialog .dialog_c{
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}
.dialog .dialog_c .messages{
    width: 50%;
    border-radius: 15px;
    padding: 15px;
}
.dialog .m_text{
    margin-bottom: 5px;
    background-color: rgb(127, 170, 187);
    padding: 10px;
}
.type_m{
    margin-left: 425px;
}
.type_m input{
    padding: 5px;
}
.type_m button{
    padding: 5px;
    display: block;
    margin-top: 5px;
}
.dialog .user{
    margin-bottom: 5px;
    padding: 10px;
}
.dialog .user a{
    color: white;
    text-decoration: none;
}
.dialog .authors{
    margin-top: 15px;
}
.delete{
    margin-left: 93%;
    color: red;
    background: none;
    border: none;
    outline: none;
    font-size: 25px;
    font-weight: bold;
}